@use "sass:math";

.main-slider {
  background-color: #fcf8f3;

  &__container {
    position: relative;

    @media (min-width: $md2+px) {
      &::before {
        content: "";
        position: absolute;
        top: 0;
        right: 376px;
        width: 100vw;
        height: 100%;
        background: #f9f1e7;
      }
    }
  }

  &__body {
    position: relative;
    z-index: 2;
    padding-top: 140px;

    @media (max-width: $md2+px) {
      padding-top: 80px;
    }
    @media (max-width: $md3+px) {
      padding-top: 50px;
    }
    @media (min-width: $md2+px) {
      display: flex;
    }
  }

  &__content {
    align-self: flex-start;
    flex: 0 0 math.div(494, 1240) * 100%;
    @include adaptive-value("margin-bottom", 80, 15, 1);
  }

  &__slider {
    flex: 1 1 auto;
    min-width: 0;
    @media (min-width: $md2+px) {
      padding-left: 32px;
      align-self: flex-end;
    }
  }
}
.content-main {
  position: relative;
  z-index: 5;
  padding-right: 15px;
  padding-left: 15px;
  @include adaptive-value("padding-top", 60, 15, 1);
  @include adaptive-value("padding-bottom", 60, 15, 1);
  background-color: $color-white;
  @media (min-width: $md2+px) {
    @include adaptive-value("padding", 60, 1, 1);
    background-color: rgba($color-white, 9);
    @supports ((-webkit-backdrop-filter: blur(31px)) or (backdrop-filter: blur(31px))) {
      background-color: rgba($color-white, 0.72);
      backdrop-filter: blur(31px);
    }
  }
  &__title {
    font-weight: 700;
    @include adaptive-value("font-size", 56, 24, 1);
    line-height: 120%;
    &:not(:last-child) {
      margin-bottom: math.div(16, 56) * 1em;
    }
  }

  &__text {
    color: $color-gray;
    @include adaptive-value("font-size", 20, 16, 1);
    line-height: 150%;
    font-weight: 500;
    &:not(:last-child) {
      margin-bottom: math.div(48, 20) * 1em;
    }
  }

  &__btn {
    font-weight: 600;
    @include adaptive-value("min-height", 78, 45, 1);
    @include adaptive-value("font-size", 20, 16, 1);
    background-color: $color-orange;
    color: $color-white;
    width: 100%;
    @media (any-hover: hover) {
      &:hover {
        background-color: darken($color-orange, 10%);
      }
    }
  }
}
.slider-main {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  &__controls {
    align-self: stretch;
    margin-bottom: 20px;
  }

  &__body {
    overflow: visible;
    @media (min-width: $md2+px) {
      @include adaptive-value("width", 934, 290, 1);
    }
  }

  &__slide {
    position: relative;
  }

  &__img {
    padding-bottom: 67%;
  }

  &__content {
    position: absolute;
    min-width: 395px;
    max-height: 300px;
    overflow: auto;
    color: inherit;
    display: flex;
    flex-direction: column;
    @include adaptive-value("padding", 24, 15, 1);
    @supports ((-webkit-backdrop-filter: blur(31px)) or (backdrop-filter: blur(31px))) {
      background-color: rgba($color-white, 0.72);
      backdrop-filter: blur(31px);
    }
    @media (min-width: $md3+px) {
      position: absolute;
      min-width: 350px;
      @include adaptive-value("right", 40, 0, 1);
      @include adaptive-value("bottom", 40, 0, 1);
    }
  }

  &__title {
    @include adaptive-value("font-size", 28, 22, 1);
    font-weight: 600;
    line-height: 120%;
    &:not(:last-child) {
      margin-bottom: math.div(33, 28) * 1em;
    }
  }

  &__text {
    line-height: 150%;
    color: gray;
    &:not(:last-child) {
      margin-bottom: 0px 0px math.div(8, 16) * 1em 0px;
    }
  }

  &__price {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
    line-height: 150%;
    @include adaptive-value("font-size", 20, 16, 1);

    &::before {
      margin-left: 10px;
      font-size: 12px;
    }
  }
}
.controls-slider-main {
  display: flex;
  align-items: center;
  &__dots {
    flex: 1 1 auto;
  }

  &__arrows {
  }
}
