.search-form {
  max-width: 473px;
  @include adaptive-value("padding-right", 32, 15, 1);

  &.--active {
    .search-form__item {
      opacity: 1;
      visibility: visible;
      top: 100%;
    }
    .search-form__icon {
      color: $color-orange;
    }
  }

  &__item {
    background-color: $color-white;
    display: flex;
    align-items: center;

    @media (max-width: $md2+px) {
      position: absolute;
      box-shadow: 0 0 5px rgba(122, 209, 255, 0.3);
      width: 100%;
      left: 0;
      top: -100%;
      z-index: 5;
      opacity: 0;
      visibility: hidden;
      transition: all 0.3s ease-in;
    }
  }

  &__icon {
    position: relative;
    z-index: 5;
    font-size: 22px;
    color: #262f56;
    @media (min-width: $md2+px) {
      display: none;
    }
  }

  &__btn {
    flex: 0 0 45px;
    height: 45px;
    font-size: 15px;
    color: #333333;
  }

  &__input {
    padding-right: 12px;
    font-size: 14px;
    color: $color-dark-gray;
    width: 100%;
  }
}
