.menu {
  flex: 0 1 420px;

  &__body {
    @media (max-width: $md3+px) {
      position: fixed;
      width: 100%;
      height: 100%;
      overflow: auto;
      top: 0;
      left: -100%;
      background-color: #fcf8f3;
      transition: left 0.3s ease-in;
      padding: 70px 15px 30px 15px;

      &::before {
        content: "";
        position: fixed;
        width: 100%;
        height: 50px;
        top: 0;
        left: -100%;
        background-color: #fcf8f3;
        transition: left 0.3s ease-in;
        z-index: 2;
      }

      &.--active {
        left: 0;
        &::before {
          left: 0;
        }
      }
    }
  }

  &__list {
    @media (min-width: $md3+px) {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-evenly;
    }
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 0 3px;
    padding: 5px 0;
    position: relative;

    @media (any-hover: hover) {
      &:hover {
        .menu__sub-list {
          transform: translate(0px, 0px);
          opacity: 1;
          visibility: visible;
          pointer-events: auto;
        }
        .menu__arrow {
          transform: rotate(-180deg);
        }
        .menu__arrow,
        .menu__link {
          color: $color-orange;
        }
      }
    }

    @media (min-width: $md3+px) {
      &.--hover {
        .menu__sub-list {
          transform: translate(0px, 0px);
          opacity: 1;
          visibility: visible;
          pointer-events: auto;
        }
        .menu__arrow {
          transform: rotate(-180deg);
        }
        .menu__link,
        .menu__arrow {
          color: $color-orange;
        }
      }
    }
    @media (max-width: $md3+px) {
      margin: 0 0 15px 0;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__link {
    font-weight: 500;
    color: inherit;
    transition: color 0.3s ease-in;
    @media (max-width: $md3+px) {
      font-size: 24px;
    }
  }

  &__arrow {
    margin-left: 8px;
    transition: all 0.3s ease-in;
    font-size: 8px;

    @media (max-width: $md3+px) {
      font-size: 14px;

      &.--active {
        transform: rotate(-180deg);
      }
    }
  }

  &__sub-list {
    position: relative;
    background-color: $color-orange;
    padding: 15px;
    flex: 1 1 100%;

    @media (min-width: $md3+px) {
      opacity: 0;
      visibility: hidden;
      transition: all 0.3s ease-in;
      transform: translate(0px, 10px);
      position: absolute;
      min-width: 200px;
      pointer-events: none;
      left: 0;
      top: 100%;
    }

    @media (max-width: $md3+px) {
      margin: 10px -15px 0 -15px;
    }
  }

  &__sub-item {
    &:not(:last-child) {
      margin-bottom: 5px;
    }

    @media (max-width: $md3+px) {
      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }
  }

  &__sub-link {
    color: $color-white;
    line-height: 120%;

    &:hover {
      text-decoration: underline;
    }

    @media (max-width: $md3+px) {
      font-size: 18px;
    }
  }
}
