@use "sass:math";

.header {
  position: relative;
  left: 0;
  top: 0;
  width: 100%;

  &__wrapper {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 50;
  }

  &__body {
    display: flex;
    align-items: center;
    padding: 5px 0;
    min-height: 125px;

    @media (max-width: $md2+px) {
      min-height: 80px;
    }
    @media (max-width: $md3+px) {
      min-height: 50px;
    }
  }

  &__main {
    display: flex;
    align-items: center;
    flex: 0 0 math.div(494, 1240) * 100%;

    @media (max-width: $md2+px) {
      flex: 1 1 auto;
    }
  }

  &__logo {
    font-size: 24px;
    line-height: math.div(29, 24) * 100%;
    font-weight: 700;
    color: $color-black;
    position: relative;
    z-index: 5;
  }

  &__search {
    @media (min-width: $md2+px) {
      flex: 1 1 auto;
    }
  }
}
