@use 'sass:math';

.--footer-title {
  @include adaptive-value("font-size", 24, 20, 1);
  line-height: math.div(29, 24) * 100%;
  font-weight: 700;
  color: $color-black;
  margin-bottom: 1em;
}

.slider-arrows {
  display: grid;
  grid-template-columns: repeat(2, 48px);
  column-gap: 8px;
}
.slider-arrow {
  border-radius: 50%;
  height: 48px;
  background-color: rgba($color-orange, 0.3);
  color: $color-orange;
  font-size: 12px;
  cursor: pointer;
  transition: all 0.3s ease-in;
  &_prev {
    &::before {
      transform: rotate(90deg);
    }
  }
  &_next {
    &::before {
      transform: rotate(-90deg);
    }
  }
  @media (any-hover: hover) {
    &:hover {
      background-color: rgba($color-orange, 1);
      color: $color-white;
    }
  }
  &_white {
    box-shadow: 0px 4px 14px 1px rgba($color-black, 0.16);
    background-color: $color-white;
    @media (any-hover: hover) {
      &:hover {
        background-color: $color-orange;
        color: $color-white;
      }
    }
  }
}
.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 24px;
  text-align: center;
  transition: all 0.3s ease-in;
}
.swiper-pagination-bullets {
  display: flex;
  flex-wrap: wrap;
}
.swiper-pagination-bullet {
  cursor: pointer;
  flex: 0 0 27px;
  height: 27px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2px 0;
  &:not(:last-child) {
    margin-right: 12px;
  }
  &::after {
    content: "";
    width: 11px;
    height: 11px;
    background-color: #d8d8d8;
    border-radius: 50%;
    transition: background-color 0.3s ease-in;
  }
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 1px solid $color-orange;
    transition: transform 0.3s ease-in;
    transform: scale(1);
  }
}
.swiper-pagination-bullet-active {
  &::after {
    background-color: $color-orange;
  }
  &::after {
    transform: scale(1);
  }
}
