@use "sass:math";

.footer {
  &__container {
  }

  &__body {
    display: grid;
    grid-template-columns: 250px 1fr 290px;
    border-top: 1px solid #d8d8d8;
    @include adaptive-value("padding-top", 80, 30, 1);
    @include adaptive-value("padding-bottom", 60, 30, 1);
    @include adaptive-value("column-gap", 80, 0, 1);

    @media (max-width: $md1+px) {
      grid-template-rows: auto auto;
      grid-template-columns: 1fr 290px;
    }

    @media (max-width: $md2+px) {
      display: block;
    }
  }

  &__main {
    display: flex;
    flex-direction: column;
    @media (max-width: $md1+px) {
      grid-column: span 2;
      @include adaptive-value("margin-bottom", 40, 30, 1);
    }
  }

  &__logo {
    @include adaptive-value("margin-bottom", 16, 8, 1);
  }

  &__text {
    color: $color-gray;
    margin-bottom: 1em;
    line-height: 150%;
  }

  &__menu {
    @media (max-width: $md2+px) {
      @include adaptive-value("margin-bottom", 40, 30, 1);
    }
  }
}

.contacts-footer {
  &__list {
    display: flex;
    flex-direction: column;
  }
  &__item {
    &:not(:last-child) {
      margin-bottom: 1em;
      @include adaptive-value("margin-bottom", 16, 10, 1);
    }
  }
  &__link {
    display: inline-flex;
    align-items: center;
    line-height: 150%;
    color: $color-gray;
    transition: color 0.3s ease-in;
    &::before {
      margin-right: 8px;
    }

    @media (any-hover: hover) {
      &:hover {
        color: $color-orange;
      }
    }
  }
}

.menu-footer {
  display: flex;
  justify-content: space-between;
  @media (max-width: $md3+px) {
    flex-direction: column;
  }
  &__column {
    &:not(:last-child) {
      @include adaptive-value("margin-bottom", 40, 30, 1);
    }
  }
  &__list {
    display: flex;
    flex-direction: column;
  }
  &__item {
    &:not(:last-child) {
      @include adaptive-value("margin-bottom", 16, 10, 1);
    }
  }
  &__link {
    color: $color-gray;
    line-height: 150%;
    font-weight: 500;

    @media (any-hover: hover) {
      &:hover {
        color: $color-orange;
      }
    }
  }
}

.subscribe {
  &__form {
    display: flex;
    align-items: center;
  }

  &__input {
    background-color: #f4f5f7;
    color: $color-gray;
    padding: 10px 0 10px 12px;
    width: 100%;
    height: 45px;
  }

  &__btn {
    flex: 0 0 45px;
    height: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $color-orange;
    margin-left: 4px;
    cursor: pointer;
    font-size: 18px;
    color: $color-white;
    transition: background-color 0.3s ease-in;
    @media (any-hover: hover) {
      &:hover {
        background-color: darken($color-orange, 10%);
      }
    }
  }
}
